body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

select, button, a {
  font-family: 'Open Sans', sans-serif;
}

/* Buttons ---------------- */
button {
  height: 32px;
  font-size: 14px;
  font-weight: 600;
  color: #326ADF;
  border: 2px solid #326ADF;
  box-shadow: none;
  outline: none;
  cursor: pointer;
  background: #FFFFFF;
  margin: 0px;
}

button:not(.nav-button) {
  border-radius: 6px;
}

button.small {
  height: 24px;
  font-size: 11px;
  border-radius: 4px;
  border-width: 1px;
  border-style: solid
}

button:disabled {
  cursor: default;
}

a {
  text-decoration: none;
  color: inherit;
}
.App-alert {
  font-size: 12px;
  padding: 4px 8px;
  margin: 8px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.App-alert p {
  margin-right: 8px;
}

.App-alert a {
  text-decoration: underline;
}
.App-footer {
  font-size: 11px;
  margin: 8px 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.App-footer a {
  margin: 0px 4px;
}
.App-container {
  width: 320px;
}
.App-nav {
  margin-bottom: 8px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border: 2px solid;
  border-radius: 6px;
  box-sizing: border-box;
}

.App-nav button {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0px !important;
}

.App-nav button {
  border-width: 0px 2px 0px 0px;
  border-radius: 0px;
}

.App-nav button:first-child {
  border-radius: 4px 0px 0px 4px;
}

.App-nav button:last-child {
  border-right: none;
  border-radius: 0px 4px 4px 0px;
}

/* ---------------- App Day/Night Styles ---------------- */
.App.day .App-nav button.selected {
  color: #FFFFFF;
  background: #326ADF;
}

.App.night .App-nav button.selected {
  color: #000000;
  background: #FFFFFF;
}
.App-slider {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: 14px;
  font-weight: 600;
  height: 32px;
  padding: 0px 6px;
  margin-bottom: 8px;
  border: 2px solid;
  border-radius: 4px;
  box-sizing: border-box;
}

.App-slider label {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.App-slider .control,
.App-slider .control-value {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex: 2 1;
          flex: 2 1;
}

.App-slider .control input {
  margin: 0px 4px;
}

.App-slider .control-value {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.App-toolbar {
  margin-bottom: 8px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.App-toolbar select,
.App-toolbar button {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.App-toolbar button {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 4px;
  padding: 0px;
}

.App-toolbar button:not(:first-child) {
  margin-left: 6px;
}
.App-select {
  position: relative;
}

.App-select select {
  width: 100%;
  height: 32px;
  font-size: 14px;
  font-weight: 600;
  border-width: 2px;
  border-style: solid;
  border-radius: 4px;
  padding: 4px 6px;
  margin: 0px;
  outline: none;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.App-select .caret {
  position: absolute;
  right: 8px;
  top: 11px;
}

/* ---------------- App Day/Night Styles ---------------- */
.App.day .App-select select {
  background: #FFFFFF;
  border-color:#326ADF;
  color: #326ADF;
}

.App.night .App-select select {
  background: #000000;
  border-color:#FFFFFF;
  color: #FFFFFF;
}
.App-radius {
  height: 18px;
  width: 18px;
  border: 2px solid #F5B400;
  border-radius: 50%;
  position: relative;
}

.App-radius .center {
  background-color: #F5B400;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  position: absolute;
  top: 7px;
  right: 7px;
}

.App-radius .radius {
  background-color: #F5B400;
  width: 8px;
  height: 2px;
  position: absolute;
  top: 8px;
  right: 0px;
}

.App-radius.white {
  border-color: #FFFFFF;
}

.App-radius.white .center {
  background-color: #FFFFFF;
}

.App-radius.white .radius {
  background-color: #FFFFFF;
}

.App-radius.black {
  border-color: #000000;
}

.App-radius.black .center {
  background-color: #000000;
}

.App-radius.black .radius {
  background-color: #000000;
}
.App-spinner {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.App-spinner img {
  margin: 16px;
}
.App-place.empty {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.App-place.empty.location {
  font-size: 12px;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.App-place.location button {
  margin: 8px;
}

.App-place.empty.request button {
  color: #FFFFFF;
  background-color: #326ADF;
  border-color: #FFFFFF;
  width: 64px;
}
.App-place {
  width: 320px;
  height: 320px;
  margin: 16px 8px;
  border: 2px solid;
  border-radius: 50%;
  padding: 4px;
  text-align: center;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: default;
  position: relative;
}

.App-place .distance {
  min-height: 43px;
  font-size: 32px;
  margin: 16px;
}

.App-place .name {
  font-size: 16px;
  font-weight: 600;
  text-align: center;
  white-space: normal;
  overflow: hidden;
}

.App-place .detail {
  min-height: 109px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: center;
          align-items: center;
  margin: 8px;
}

.App-place .detail div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 4px;
}

.App-place .detail .hours {
  -webkit-flex-direction: column;
          flex-direction: column;
}

.App-place .price {
  font-size: 16px;
  font-weight: 600;
}

.App-place .detail input {
  height: 0px;
  opacity: 0;
}

.App-place .detail button {
  margin: 0px 2px;
}
.App-places {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
}

.App-places .price-level {
  font-size: 24px;
}

.App-places .price-level span {
  position: absolute;
  padding-bottom: 2px;
}

.App-places .attributions {
  text-align: center;
  font-size: 11px;
}

.App-places .attributions img {
  margin-top: 4px; 
  display: block;
}

.App {
  font-size: 14px;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  color: #326ADF;
}

.App-policy {
  font-size: 12px;
  padding: 0px 8px;
}

/* ------------- Special Styles ------------- */
.App.day button.yellow,
.App.night button.yellow {
  color: #F5B400;
  border-color: #F5B400;
}

.App.day button.selected.yellow,
.App.night button.selected.yellow  {
  background: #F5B400;
}

.App.day button.green,
.App.night button.green {
  color: #35A251;
  border-color: #35A251;
}

.App.day button.selected.green,
.App.night button.selected.green  {
  background: #35A251;
}

/* ---------------- App Day Styles ---------------- */
.App.day {
  background: #FFFFFF;
  color: #326ADF;
}

.App.day button {
  background: #FFFFFF;
  color: #326ADF;
  border-color: #326ADF;
}

.App.day button.selected {
  background: #326ADF;
  color: #FFFFFF;
}

.App.day a {
  color: #326ADF;
}

.App.day input {
  background: #FFFFFF;
  border: 1px solid #326ADF;
}

/* ---------------- App Night Styles ---------------- */
.App.night {
  background: #000000;
  color: #FFFFFF;
}

.App.night button {
  background: #000000;
  color: #FFFFFF;
  border-color: #FFFFFF;
}

.App.night button.selected {
  background: #FFFFFF;
  color: #000000;
}

.App.night a {
  color: #FFFFFF;
}

.App.night input {
  background: #000000;
  color: #FFFFFF;
  border: 1px solid #FFFFFF;
}

